<template>
  <footer @click="toHello">
    <p>{{copyright}}</p>
  </footer>
</template>

<script>
    export default {
        name: "Footer",
        data() {
            return {
                copyright: 'Copyright 2019 shenke'
            }
        },
        props: {},
        components: {},
        methods: {
            toHello: function () {
                this.$router.push({
                    name: 'hello',
                    params: {
                        message: 'the message from footer'
                    }
                })
            }
        },
        computed: {},
        watch: {},
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  footer {
    padding: 5px;
    background: whitesmoke;
    text-align: center;
  }

  footer p {
    color: black;
  }

</style>



