<template>
  <div class="">
    {{message}}
  </div>
</template>

<script>
    export default {
        name: "Hello",
        data() {
            return {
                message: undefined
            }
        },
        props: {},
        components: {},
        methods: {},
        computed: {},
        watch: {},

        created() {
            this.message = this.$route.params.message;
        }
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
</style>



