<template>
  <div class="home">
    <div @click="showComments">
      <Header></Header>
    </div>
    <Users :users="users"></Users>
    <Footer></Footer>
  </div>
</template>

<script>
    import Header from "./Header";
    import Footer from "./Footer";
    import Users from "./Users";

    export default {
        name: "Home",
        data() {
            return {
                users: [],
            }
        },
        props: {},
        components: {Header, Users, Footer},
        methods: {
            showComments: function () {
                console.log('showComments');
                this.$axios.get('http://jsonplaceholder.typicode.com/comments').then(data => {
                    var status = data.status;
                    if (status == 200) {
                        this.users = [];
                        this.users = data.data;
                    } else {
                        alert('axios get comments error');
                    }
                })
            }
        },
        computed: {},
        watch: {},
        created() {
            this.$http.get('http://jsonplaceholder.typicode.com/users').then((data) => {
                var status = data.status;
                if (status == 200) {
                    this.users = data.data;
                } else {
                    alert('get users error');
                }
            })
        },
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
</style>



